import React from 'react'
import { HashRouter as Router, Route, Switch } from 'react-router-dom'
import Home from './view/home'
import SongMenu from './view/songMenu'
import SingerDetails from './view/singerDetails'
import PlayMusic from './view/playMusic'
import Search from './view/search'
import './APP.scss'

export default class App extends React.Component {
  render() {
    return (
      <div>
        <div className='box'>
            <Router>
              <Route path='/' component={Home} />
              <Switch>
                <Route path='/songMenu/:id' component={SongMenu} />
                <Route path='/singerDetails/:id' component={SingerDetails} />
                <Route path='/search' component={Search} />
              </Switch>
            </Router>
            <PlayMusic />
        </div>
      </div>
    )
  }
}
