import request from '../utils/request';

function searchMusic(keywords) {
    return request({
        method: 'get',
        url: '/search',
        params:{
            keywords,
            t: Date.now()
        }
    });
}


function getSearchHot() {
    return request({
        method: 'get',
        url: '/search/hot',
        params:{
            t: Date.now()
        }
    });
}

export {
    getSearchHot,
    searchMusic
}