import request from '../utils/request';

function singerType({cat,initial}) {
    return request({
        method: 'get',
        url: '/artist/list',
        params:{
            cat,
            initial,
            t: Date.now()
        }
    });
}

function getSongList({id}) {
    return request({
        method: 'get',
        url: '/artists',
        params:{
            id,
            t: Date.now()
        }
    });
}
function getSingerDesc(id) {
    return request({
        method: 'get',
        url: '/artist/album',
        params:{
            id,
            t: Date.now()
        }
    });
}

export {
    singerType,
    getSongList,
    getSingerDesc
}