import request from '../utils/request'

function getHotSongMenu() {
  return request({
    method: 'get',
    url: '/personalized',
    params: {
      t: Date.now()
    }
  })
}

function getDetails(id) {
  return request({
    method: 'get',
    url: '/playlist/detail',
    params: {
      id,
      t: Date.now()
    }
  })
}

function getPlaylist(id) {
  return request({
    method: 'get',
    url: '/simi/playlist',
    params: {
      id,
      t: Date.now()
    }
  })
}

export { getHotSongMenu, getDetails, getPlaylist }
