import request from '../utils/request';

function getMusicUrl(id) {
    return request({
        method: 'get',
        url: '/song/url',
        params:{
            id,
            t: Date.now()
        }
    });
}
function getMusicLyc(id) {
    return request({
        method: 'get',
        url: '/lyric',
        params:{
            id,
            t: Date.now()
        }
    });
}

export {
    getMusicUrl,
    getMusicLyc,

}