import React from 'react'
import TopBar from './components/topBar'
import './index.scss'
import { Tabs, WhiteSpace } from 'antd-mobile'
import Recommend from './components/recommend'
import Singer from './components/singer'
import Ranking from './components/ranking'

const tabs = [{ title: '推荐', sub: '1' }, { title: '歌手', sub: '2' }, { title: '排行榜', sub: '3' }]

export default class home extends React.Component {
  render() {
    return (
      <div className='home'>
        <TopBar />
        <Tabs
          tabs={tabs}
          initialPage={0}
          tabBarBackgroundColor='#d44439'
          tabBarInactiveTextColor='#e4e4e4'
          tabBarActiveTextColor='#f1f1f1'
          swipeable={true}
          prerenderingSiblingsNumber={false}
        >
          <div className='tabBox' ref={val => (this.recommend = val)}>
            <Recommend isLoad={() => this.$lazyLoading(this.recommend)} />
            {/* 推荐页面 */}
          </div>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              height: '100%',
              backgroundColor: '#f2f3f4'
            }}
          >
            <div className='tabBox' ref={val => (this.singer = val)}>
              <Singer isLoad={()=> this.$lazyLoading(this.singer)}/>
              {/* 歌手页面 */}
            </div>
          </div>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              height: '100%',
              backgroundColor: '#f2f3f4'
            }}
          >
            <div className='tabBox' ref={val => this.ganking = val}>
              <Ranking isLoad={()=> this.$lazyLoading(this.ganking)}/>
              {/* 排行榜页面 */}
            </div>
          </div>
        </Tabs>
        <WhiteSpace />
      </div>
    )
  }
}
