
const placeholder = "https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=1897943806,295048940&fm=26&gp=0.jpg"
const windowHei = window.innerHeight + 100

function isLoad(ele, Tiem) {
  if (!Tiem) {
    Tiem = setTimeout(() => {
      let imgArr = ele.querySelectorAll('[data-url]')
      imgArr.forEach(item => {
        if (item.y < windowHei) {
          item.setAttribute('src', item.getAttribute('data-url'))
          item.removeAttribute('data-url')
        }
      })
      clearInterval(Tiem)
      Tiem = null
    }, 200)
  }
}

function lazyLoading(ele) {
  let Tiem = null
  let imgArr = ele.querySelectorAll('[data-url]')
  imgArr.forEach((item) => {
    if (item.getAttribute('src') === '') {
      item.setAttribute('src', placeholder)
    }
  })
  isLoad(ele, Tiem)
  ele.addEventListener('scroll', () => {
    isLoad(ele, Tiem)
  })
}

export  {lazyLoading}
