import React from 'react'
import './index.scss'
import { getHotSongMenu } from '../../../../../api/song_menu'
import {  Grid } from 'antd-mobile'
import { withRouter } from 'react-router-dom'

class songList extends React.Component {
  state = {
    songList: []
  }
  componentDidMount() {
    this.getSongList()
  }
  songMenu(item) {
    this.props.history.push('/songMenu/' + item.id)
  }
  async getSongList() {
    try {
      let res = await getHotSongMenu()
      this.setState(
        {
          songList: res.result
        },
        () => this.props.isLoad()
      )
    } catch (err) {
      console.log(err)
    }
  }
  render() {
    return (
      <div className='songList' ref={val => (this.recommend = val)}>
        <div className='title'>推荐歌单</div>
        <Grid
          hasLine={false}
          data={this.state.songList}
          columnNum={3}
          square={false}
          renderItem={item => (
            <div onClick={() => this.songMenu(item)} className="itemBox">
              <div className='imgBox'>
                <img src='' alt='' data-url={item.picUrl} />
              </div>
              <div className="text">{item.name}</div>
              <i className='iconfont icon-kefu'> {Math.ceil(item.playCount / 10000)}万</i>
            </div>
          )}
        />
      </div>
    )
  }
}

export default withRouter(songList)
