import React from 'react';
import './index.scss';
import { Icon, Button, List } from 'antd-mobile';
import { getSongList } from '../../api/singer';
import store from '../../redux';
import {setMucData} from '../../redux/actionCreator';
const Item = List.Item;
const Brief = Item.Brief;

export default class singerDetails extends React.Component {
    state = {
        songList: {
            artist: {
                picUrl:
                    'http://img3.imgtn.bdimg.com/it/u=1105085267,629346802&fm=26&gp=0.jpg',
                name: '歌单'
            },
            hotSongs: []
        },
        show: {}
    };
    componentDidMount() {
        this.getSongList();
        setTimeout(() => {
            this.setState({ show: { transform: 'rotate(0deg)' } });
        }, 0);
    }

    // 获取歌曲数据列表
    async getSongList() {
        try {
            let res = await getSongList({
                id: this.props.match.params.id
            });
            this.setState({ songList: res });
        } catch (err) {
            console.log(err);
        }
    }
    // 播放音乐
    play(list,index,e){
        let arr = [];
        list.forEach((item) => {
            arr.push({
                id:item.id,
                name:item.name, 
                artId:item.ar[Math.floor(Math.random()*item.ar.length)].id,
                arName:item.ar[0].name
            });
        });
        store.musicUrl.dispatch(setMucData({ list: arr, index, even: e }))
    }
    render() {
        return (
            <div
                id="singerDetails"
                className="singerDetails"
                style={this.state.show}
            >
                <div
                    className="cover"
                    style={{
                        backgroundImage: `url(${this.state.songList.artist.picUrl})`
                    }}
                >
                    <Button className="btn" inline type="warning">
                        <i className="iconfont icon-add"> 收藏</i>
                    </Button>
                </div>
                <div id="songMenuList" className="songMenuList">
                    <List>
                        <div className="title">
                            <div>
                                <i className="iconfont icon-bofang" onClick={(e) => this.play(this.state.songList.hotSongs,0,e)}></i>
                                <span>
                                    播放全部
                                    <span>
                                        {this.state.songList.hotSongs &&
                                            `(共${this.state.songList.hotSongs.length}首)`}
                                    </span>
                                </span>
                            </div>
                        </div>
                        {this.state.songList.hotSongs.map((item, index) => {
                            return (
                                <div className="box" key={index}>
                                    <span className="index">{index + 1}</span>
                                    <Item multipleLine onClick={(e) => this.play(this.state.songList.hotSongs,index,e)}>
                                        {item.name}
                                        <Brief>
                                            {item.ar[0].name} - {item.name}
                                        </Brief>
                                    </Item>
                                </div>
                            );
                        })}
                    </List>

                    {this.state.songList.hotSongs.length === 0 && (
                        <Icon type="loading" />
                    )}
                </div>

                <div className="top">
                    <Icon
                        type="left"
                        onClick={() => {
                            this.setState({
                                show: { transform: 'rotate(-90deg)' }
                            });
                            setTimeout(() => {
                                this.props.history.go(-1);
                            }, 400);
                        }}
                    />
                    {this.state.songList.artist.name}
                </div>
            </div>
        );
    }
}
